<template>
  <div class="container">
    <div class="text-end mt-4">
      <button class="btn btn-primary" @click="showModal('new')">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.origin_price }}</td>
          <td>{{ item.price }}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-outline-primary btn-sm"
                @click="showModal('edit', item)"
              >
                
              </button>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm"
                @click="showModal('delete', item)"
              >
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
  <showmodal ref="productModal" :temp="tempProduct" :is-new="isNew"></showmodal>
</template>

<script>
import showmodal from '../../components/editModal.vue'
export default {
  data () {
    return {
      apiUrl: 'https://vue3-course-api.hexschool.io',
      apiPath: 'ffjjgogogo',
      products: [],
      page: 0,
      isNew: true,
      tempProduct: {
        imagesUrl: []
      }
    }
  },
  components: {
    // delmodal,
    showmodal
    // pagination
  },
  created () {
    // token
    const token = document.cookie.replace(
      /(?:(?:^|.*;\s*)hextoken\s*=\s*([^;]*).*$)|^.*$/,
      '$1'
    )
    if (!token) {
      alert('token')
      // window.location = './w4_mainHW_login.html'
    }

    // tokenapi header 
    this.$http.defaults.headers.common.Authorization = token

    // 
    this.getData()
  },
  methods: {
    getData () {
      this.$http
        .get(`${this.apiUrl}/api/${this.apiPath}/admin/products?page=1`)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            console.log(res.data.products)
            // console.log(res.data.pagination)
            this.products = res.data.products
            this.page = res.data.pagination
          } else {
            alert(res.data.message)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    // modalrefs
    showModal (detect, item) {
      if (detect === 'delete') {
        this.tempProduct = {}
        this.tempProduct = {
          ...item
        }
        this.$refs.deleteModal.openDelModal()
      } else if (detect === 'edit') {
        this.isNew = false
        this.tempProduct = {}
        this.tempProduct = {
          ...item
        }
        // console.log(this.tempProduct)
        this.$refs.productModal.openProductModal()
      } else if (detect === 'new') {
        this.isNew = true
        this.tempProduct = {
          imagesUrl: []
        }
        this.$refs.productModal.openProductModal()
      }
    }
  }
}
</script>

<style></style>
