<template>
  <div
    id="productModal"
    ref="productModal"
    class="modal fade"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 id="productModalLabel" class="modal-title">
            <span v-if="isNew"></span>
            <span v-else></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-1">
                <div class="form-group">
                  <div class="mb-3"></div>
                  <!--   -->
                  <label for="imageUrl"></label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="temProduct.imageUrl"
                  />
                </div>
                <img class="img-fluid" :src="temProduct.imageUrl" alt="" />
                <br />
                <!--  -->
                <div class="mb-1 mt-3"></div>
                <!-- tempProductimagesUrl() -->
                <div v-if="Array.isArray(tempProduct.imagesUrl)">
                  <div
                    class="form-group"
                    v-for="(img, index) in tempProduct.imagesUrl"
                    :key="index"
                  >
                    <br />
                    <label for="imageUrl">{{ index + 1 }}</label>
                    <input
                      type="text"
                      v-model="tempProduct.imagesUrl[index]"
                      :id="`image${index}`"
                      class="form-control"
                      placeholder=""
                    />
                    <img class="img-fluid" :src="img" alt="" />
                  </div>
                  <br />
                  <!-- () -->
                  <div
                    v-if="
                      !tempProduct.imagesUrl.length ||
                      tempProduct.imagesUrl[tempProduct.imagesUrl.length - 1]
                    "
                  >
                    <button
                      class="btn btn-outline-primary btn-sm d-block w-100"
                      @click="tempProduct.imagesUrl.push('')"
                    >
                      <!-- pushinputtempProduct -->
                      <!--  -->
                      
                    </button>
                  </div>

                  <!-- ""pushtempProduct -->
                  <div v-else>
                    <button
                      class="btn btn-outline-danger btn-sm d-block w-100"
                      @click="tempProduct.imagesUrl.pop()"
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
              <!--  -->
              <div>
                <button class="btn btn-outline-primary btn-sm d-block w-100">
                  
                </button>
              </div>
              <div>
                <button class="btn btn-outline-danger btn-sm d-block w-100">
                  
                </button>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group">
                <label for="title"></label>
                <input
                  id="title"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="temProduct.title"
                />
              </div>

              <div class="row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input
                    id="category"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="temProduct.category"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="unit"
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="temProduct.unit"
                  />
                </div>
              </div>

              <div class="row">
                <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                  <input
                    id="origin_price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                    v-model="temProduct.origin_price"
                  />
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input
                    id="price"
                    type="number"
                    min="0"
                    class="form-control"
                    placeholder=""
                    v-model="temProduct.price"
                  />
                </div>
              </div>
              <hr />

              <div class="form-group">
                <label for="description"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="temProduct.description"
                >
                </textarea>
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea
                  id="description"
                  type="text"
                  class="form-control"
                  placeholder=""
                  v-model="temProduct.content"
                >
                </textarea>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input
                    id="is_enabled"
                    class="form-check-input"
                    type="checkbox"
                    :true-value="1"
                    :false-value="0"
                    v-model="temProduct.is_enabled"
                  />
                  <label class="form-check-label" for="is_enabled"
                    ></label
                  >
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-outline-secondary"
            data-bs-dismiss="modal"
          >
            
          </button>
          <button type="button" class="btn btn-primary"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import modalMixin from '@/mixins/modalMixin'
import Modal from 'bootstrap/js/dist/modal'
export default {
  data () {
    return {
      productModal: '',
      temProduct: {}
    }
  },
  //  mixins: [modalMixin],
  props: ['isNew', 'temp'],
  mounted () {
    // Modal
    console.log(this.$refs)
    // refsDom
    this.productModal = new Modal(this.$refs.productModal)
    // this.temProduct = { ...this.temp }
  },
  watch: {
    temp () {
      this.temProduct = this.temp
    }
  },
  methods: {
    openProductModal () {
      console.log(this.temp)
      this.productModal.show()
    },
    closeProductModal () {
      this.modal.hide()
    }
  }
}
</script>

<style></style>
